import iconImg from "../../assets/icon.png"
import { Container, Content, TextContent } from "./styles";
export function About(){
  return(
    <Container>
      <Content>
        <img src={iconImg} alt="Logo" />
        <TextContent>
          <h1>Conhea o PETComp</h1>
          <p>O Programa de Educao Tutorial de Cincia da Computao da UFMA foi criado em 1 de Setembro de 2007, tendo como tutor o professor Alexandre Csar Muniz de Oliveira, em 19 de Janeiro de 2016 o professor Geraldo Braz Junior tornou-se o novo tutor do grupo. E aps sua excelente gesto, em 2019 assume o professor Luis Rivero. O PET Computao j realizou diversas atividades de pesquisa, ensino e extenso. Dentre algumas das atividades podemos citar minicursos, Projetos de Extenso, como por exemplo, ensino de pensamento computacional, organiza o Encontro Acadmico de Computao (EAComp), desenvolvimento de sistemas, entre outros.</p>
        </TextContent>
      </Content>
    </Container>

  );
}